import {Component, OnInit} from '@angular/core';
import { Router} from "@angular/router";
import  { Contacto } from "../../Interfaces/contacto";
import { CrudFirestoreService } from "../../Services/crud-firestore.service";
import { ContactService } from "../../Services/contact.service";

@Component({
  selector: 'app-home',
  templateUrl: 'home.page.html',
  styleUrls: ['home.page.scss'],
})
export class HomePage implements OnInit {

    private amigos_Truee: boolean = false;
    private trabajo_Truee: boolean = false;
    private social_Truee: boolean = false;

    private texto_busqueda: string = '';
    private otros: Contacto[];
    private trabajos: Contacto[];
    private amigos: Contacto[];
    private busqueda: Contacto[];
    private change_State: boolean = false;
    private pruebaFiltro: Contacto[];


    constructor(private router: Router,
                private crudService: CrudFirestoreService,
               ) {}

     ngOnInit() {
         this.crudService.get_Contacts().subscribe(res => {
             console.log(res);
             this.busqueda = res;
             this.obtener(res);
         });

     }
    buscarUsuario(event) {
        const texto = event.target.value;
        this.texto_busqueda = texto;

        if (texto === '') {
            this.change_State = false;
        }
        if (texto !== '') {
            this.change_State = true;
        }
    }
    async obtener(res) {
        this.pruebaFiltro = res;
        this.otros = await this.pruebaFiltro.filter( otros => {
           return otros.Circulo === 'Otros' || otros.Circulo === ''
        });
        this.trabajos = await this.pruebaFiltro.filter(trabajos => {
           return trabajos.Circulo === 'Trabajo'
        });
        this.amigos = await this.pruebaFiltro.filter(amigos => {
            return amigos.Circulo === 'Amigos'
        });
        console.log(this.otros, this.amigos, this.trabajos)
    }

}

